'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'

export default function RegisterPage() {
  const router = useRouter()

  const [form, setForm] = useState({
    first_name: '',
    surname: '',
    other_names: '',
    email: '',
    pen_number: '',
    password: '',
    confirm_password: '',
  })

  const [error, setError] = useState('')
  const [submitting, setSubmitting] = useState(false)

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setForm((prev) => ({ ...prev, [name]: value }))
    setError('')
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    if (form.password !== form.confirm_password) {
      setError('Passwords do not match.')
      return
    }

    setSubmitting(true)

    try {
      const res = await fetch('http://localhost:5000/api/auth/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          first_name: form.first_name,
          surname: form.surname,
          other_names: form.other_names,
          email: form.email,
          pen_number: form.pen_number,
          password: form.password,
        }),
      })

      if (res.status === 409) {
        setError('User with this PEN or Email already exists.')
        setSubmitting(false)
        return
      }

      if (!res.ok) {
        const data = await res.json()
        setError(data.error || 'Registration failed.')
        setSubmitting(false)
        return
      }

      router.push('/login')
    } catch {
      setError('Something went wrong.')
      setSubmitting(false)
    }
  }

  return (
    <div className="max-w-md mx-auto bg-white p-6 rounded-lg shadow mt-10">
      <h2 className="text-2xl font-semibold text-blue-700 text-center mb-4">User Registration</h2>

      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block font-semibold">First Name</label>
          <input type="text" name="first_name" value={form.first_name} onChange={handleChange} required className="w-full border p-2 rounded" />
        </div>

        <div>
          <label className="block font-semibold">Surname</label>
          <input type="text" name="surname" value={form.surname} onChange={handleChange} required className="w-full border p-2 rounded" />
        </div>

        <div>
          <label className="block font-semibold">Other Names (Optional)</label>
          <input type="text" name="other_names" value={form.other_names} onChange={handleChange} className="w-full border p-2 rounded" />
        </div>

        <div>
          <label className="block font-semibold">Email</label>
          <input type="email" name="email" value={form.email} onChange={handleChange} required className="w-full border p-2 rounded" />
        </div>

        <div>
          <label className="block font-semibold">PEN Number</label>
          <input type="text" name="pen_number" value={form.pen_number} onChange={handleChange} required className="w-full border p-2 rounded" />
        </div>

        <div>
          <label className="block font-semibold">Password</label>
          <input type="password" name="password" value={form.password} onChange={handleChange} required className="w-full border p-2 rounded" />
        </div>

        <div>
          <label className="block font-semibold">Confirm Password</label>
          <input type="password" name="confirm_password" value={form.confirm_password} onChange={handleChange} required className="w-full border p-2 rounded" />
        </div>

        {error && <p className="text-red-600 text-sm">{error}</p>}

        <button
          type="submit"
          disabled={submitting}
          className="w-full bg-blue-700 text-white py-2 rounded hover:bg-blue-800"
        >
          {submitting ? 'Registering...' : 'Register'}
        </button>
      </form>

      <p className="text-center text-sm text-gray-500 mt-6">&copy; 2025 Prime Pillars Real Estate</p>
    </div>
  )
}
